import React, { Component } from 'react';

import styles from './Movie.module.css';

export default class Movie extends Component {
    posterPathURL = poster_path => {
        let { secure_base_url } = this.props.configuration;
        return poster_path
            ? secure_base_url + 'w500' + poster_path
            : 'https://image.freepik.com/free-vector/man-sitting-on-big-question-mark-symbol_24381-1192.jpg';
    };

    render() {
        let {
            title,
            poster_path,
            popularity,
            release_date,
            runtime,
            production_countries,
            genres,
            budget,
            revenue,
            overview,
            homepage,
        } = this.props.data;
        console.log(this.props.data);
        return (
            <div className={styles.mainMovie}>
                <div className={styles.leftBar}>
                    <h2>{title}</h2>
                    <img src={this.posterPathURL(poster_path)} alt={title} />

                    <table>
                        <tbody>
                            <tr>
                                <td>Popularity:</td>
                                <td>{popularity}</td>
                            </tr>
                            <tr>
                                <td>Release:</td>
                                <td>{release_date}</td>
                            </tr>
                            <tr>
                                <td>Runtime:</td>
                                <td>{runtime}</td>
                            </tr>
                            <tr>
                                <td>Countries:</td>
                                <td>
                                    {production_countries
                                        .map(countri => countri.name)
                                        .join(', ')}
                                </td>
                            </tr>
                            <tr>
                                <td>Genres:</td>
                                <td>
                                    {genres.map(genr => genr.name).join(', ')}
                                </td>
                            </tr>
                            <tr>
                                <td>Budget:</td>
                                <td>
                                    {Intl.NumberFormat('ru-RU').format(budget)}
                                </td>
                            </tr>
                            <tr>
                                <td>Revenue:</td>
                                <td>
                                    {Intl.NumberFormat('ru-RU').format(revenue)}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div className={styles.righteBar}>
                    <h2>Overview:</h2>
                    <p>{overview}</p>
                    <p>
                        <a href={homepage} target="_blank" rel="noreferrer">
                            Homepage
                        </a>
                    </p>
                </div>
            </div>
        );
    }
}

Movie.defaultProps = {
    title: '???',
    popularity: '???',
    release_date: '???',
    runtime: '???',
    production_countries: [{ name: '???' }],
    genres: [{ name: '???' }],
    budget: '???',
    revenue: '???',
    overview: '???',
};
