import React, { Component, Fragment } from 'react';

import Logo from '../components/Header/Logo/Logo';
import Navigation from '../components/Header/Navigation/Navigation';
import axiosRequests from '../Requests/Requests';
import Spinner from '../components/Main/Spinner/Spinner';

import styles from '../components/Header/Navigation/Navigation.module.css';
import stylesMain from '../components/Main/Main.module.css';
import Movie from '../components/Main/Movie/Movie';
import NotFound from '../views/NotFound';

export default class MovieDetails extends Component {
    state = {
        film: '',
        error: false,
        loading: true,
        width: window.innerWidth,
    };

    componentDidMount() {
        this.setState({ loading: true });
        axiosRequests
            .axiosMovieDetails(this.props.match.params.id)
            .then(request => {
                console.log(request.data);
                this.setState({ film: request.data });
            })
            .catch(error => {
                return this.setState({ error: error });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
    }

    updSize = () => {
        this.setState({
            width: window.innerWidth,
        });
    };

    render() {
        let { film, error, width, loading } = this.state;
        console.log(error.message);
        return (
            <Fragment>
                <header>
                    <div className={styles.wrapHeader}>
                        <Logo />
                        <Navigation
                            onWidth={width}
                            onConfiguration={this.configuration}
                        />
                    </div>
                </header>
                <main>
                    {error && <NotFound />}
                    {/* {error && <h2>{error.message}</h2>} */}
                    {!loading && (
                        <div className={stylesMain.wrapMain}>
                            {film && (
                                <Movie
                                    data={film}
                                    configuration={
                                        this.props.location.configuration
                                    }
                                />
                            )}
                        </div>
                    )}
                    {loading && <Spinner />}
                </main>
                <footer></footer>
            </Fragment>
        );
    }
}
